<template>
  <el-container class="home_con">
    <el-header>
      <div>
        <img src="https://s3.ax1x.com/2020/12/24/rg34Z4.png" alt />
        <span>教务管理系统</span>
      </div>
      <el-button type="info" @click="loginOut">退出</el-button>
    </el-header>
    <el-container>
      <el-aside :width="isColl ? '64px':'200px'">
        <div class="toggle_clike" @click="coll">|||</div>
        <!-- 菜单 -->
        <el-menu
          background-color="#323744"
          text-color="#fff"
          active-text-color="#459DF7"
          :unique-opened="true"
          :collapse="isColl"
          :collapse-transition="false"
          :router="true"
          :default-active="path"
        >
          <el-submenu :index="'/'+mode.id" v-for="mode in modelist" :key="mode.id">
            <!-- 一级目录 -->
            <template slot="title">
              <i :class="mode.icon"></i>
              <span>{{mode.menuName}}</span>
            </template>
            <!-- 二级目录 -->
            <el-menu-item
              :index="'/'+itme.menuPath"
              v-for="itme in mode.children"
              :key="itme.id"
              @click="getPath('/'+itme.menuPath)"
            >
              <template slot="title">
                <i :class="itme.icon"></i>
                <span>{{itme.menuName}}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <section class="app-main">
          <transition name="el-fade-in-linear" mode="out-in">
            <router-view></router-view>
          </transition>
        </section>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
import {mapState} from "vuex"
export default {
  methods: {
    loginOut() {
      window.sessionStorage.clear()
      window.location.reload
      this.$router.push('/login')
    },
    getModeList() {
      this.path = window.sessionStorage.getItem('path')
    },
    coll() {
      this.isColl = !this.isColl
    },
    getPath(pathNew) {
       window.sessionStorage.removeItem("path");
      window.sessionStorage.setItem('path', pathNew)
      this.path = pathNew
    }
  },
  created() {
    this.getModeList()
    this.modelist=this.modueList
  },
   computed:{
     ...mapState(['modueList'])
  },
  watch:{
    $route(to,from){
      this.getModeList()
    }
  },
  data() {
    return {
      modelist: [],
      isColl: false,
      path: ''
    }
  },

}
</script>
