<template>
    <div class="login_conn">
        <div class="login_box">
            <div class="img_box">
                <img src="https://s3.ax1x.com/2020/12/24/rg3wqg.png" alt/>
            </div>
            <el-form class="form_login" :rules="loginRules" :model="user" ref="loginForm">
                <el-form-item prop="username">
                    <el-input prefix-icon="el-icon-user-solid" v-model="user.username"></el-input>
                </el-form-item>

                <el-form-item prop="password">
                    <el-input prefix-icon="el-icon-unlock" v-model="user.password" show-password>></el-input>
                </el-form-item>

                <el-form-item class="btns">
                    <el-button type="primary" @click="login">登录</el-button>
                    <el-button type="info" @click="LoginReset">重置</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>
<script>
import { mapState } from 'vuex'
  import { initRouter } from '../router/index'

  export default {
    data() {
      return {
        user: {
          username: 'admin',
          password: 'admin'
        },
        loginRules: {
          username: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
            { min: 5, max: 15, message: '长度在 5 到 15 个字符', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 5, max: 15, message: '长度在 5 到 15 个字符', trigger: 'blur' }
          ]
        }
      }
    },
    computed: {
    ...mapState(['btus']),
  },
    methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
      //   重置方面
      LoginReset() {
        this.user = {}
        this.$refs.loginForm.resetFields()
      },
      // 登录
      login() {
        this.$refs.loginForm.validate(async valid => {
          if (!valid) return
          const { data: res } = await this.$http.post('/jxyxUser/login', this.user)
          if (res.code !== 20000) return this.$message.error(res.message)
          // 存token
          window.sessionStorage.setItem('token', res.data.token)
          // console.log(res.data.modul)
          this.$store.commit('setmodueList', res.data.modul)
          this.$store.commit('setBtus', res.data.btu)
          initRouter()
          this.$router.push('/home')
          this.$message.success('登录成功')
        })
      }
    }
  }
</script>

